.ontop,
#growls-default,
#growls-tl,
#growls-tr,
#growls-bl,
#growls-br,
#growls-tc,
#growls-bc,
#growls-cc,
#growls-cl,
#growls-cr {
  z-index: 50000;
  position: fixed;
}

#growls-default {
  inset-block-start: 10px;
  inset-inline-end: 10px;
}

#growls-tl {
  inset-block-start: 10px;
  inset-inline-start: 10px;
}

#growls-tr {
  inset-block-start: 10px;
  inset-inline-end: 10px;
}

#growls-bl {
  inset-block-end: 10px;
  inset-inline-start: 10px;
}

#growls-br {
  inset-block-end: 10px;
  inset-inline-end: 10px;
}

#growls-tc {
  inset-block-start: 10px;
  inset-inline: 10px;
}

#growls-bc {
  inset-block-end: 10px;
  inset-inline: 10px;
}

#growls-cc {
  inset-block-start: 50%;
  inset-inline-start: 50%;
  margin-inline-start: -125px;
}

#growls-cl {
  inset-block-start: 50%;
  inset-inline-start: 10px;
}

#growls-cr {
  inset-block-start: 50%;
  inset-inline-end: 10px;
}

#growls-tc .growl,
#growls-bc .growl {
  margin-inline: auto;
}

.growl {
  opacity: 0.8;
  filter: alpha(opacity = 80);
  position: relative;
  border-radius: 4px;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;

  &.growl-incoming,
  &.growl-outgoing {
    opacity: 0;
    filter: alpha(opacity = 0);
  }

  &.growl-small {
    width: 200px;
    padding: 5px;
    margin: 5px;
  }

  &.growl-medium {
    width: 250px;
    padding: 10px;
    margin: 10px;
  }

  &.growl-large {
    width: 300px;
    padding: 15px;
    margin: 15px;
  }

  &.growl-default {
    color: $custom-white;
    background: #2d2d2d;
  }

  &.growl-error {
    color: $custom-white;
    background: #ff5b51;
  }

  &.growl-notice,
  &.growl-sucess {
    color: $custom-white;
    background: #2dce89;
  }

  &.growl-warning {
    color: $custom-white;
    background: #ecb403;
  }

  .growl-close {
    cursor: pointer;
    float: $float-end;
    font-size: 14px;
    line-height: 18px;
    font-weight: normal;
    font-family: helvetica, verdana, sans-serif;
  }

  .growl-title {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    margin-block-end: 5px;
  }

  .growl-message {
    font-size: 13px;
    line-height: 18px;
  }
}
html {
	box-sizing: border-box;
}
*, *::after, *::before {
	box-sizing: inherit;
}
code {
	font-family: "Consolas", "Monaco", monospace;
	font-weight: normal;
}

pre {
	overflow: scroll;
	background: $background;
	padding: 10px;
	.support {
		&.tag {
			color: #008;
		}
		&.attribute {
			color: #800;
		}
	}
	.string {
		&.value {
			color: #080;
		}
	}
}
