.formats {
	.format {
		display: inline-block;
	}
}
.zip {
	background: url("../images/zip.png");
	display: block;
	width: 128px;
	height: 128px;
}
.tar {
	background: url("../images/tar.png");
	display: block;
	width: 128px;
	height: 128px;
}
.fork {
	position: absolute;
	inset-block-start: 0;
	inset-inline-start: 0;
	border: 0;
}
