svg {
	-ms-touch-action: none;
	touch-action: none;
}
.jvectormap-container {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
}
.jvectormap-tip {
	position: absolute;
	display: none;
	border-radius: 3px;
	background: $black;
	color: $white;
	padding: 6px;
	font-size: 11px;
	line-height: 1;
	font-weight: 700;
	small {
		font-size: inherit;
		font-weight: 400;
	}
}
.jvectormap-zoomin, .jvectormap-zoomout, .jvectormap-goback {
	position: absolute;
	inset-inline-start: 10px;
	border-radius: 3px;
	background: $background;
	padding: 3px;
	color: $primary;
	cursor: pointer;
	line-height: 18px;
	font-size: 18px;
	text-align: center;
	box-sizing: content-box;
}
.jvectormap-zoomin, .jvectormap-zoomout {
	width: 20px;
	height: 20px;
}
.jvectormap-zoomin {
	inset-block-start: 10px;
}
.jvectormap-zoomout {
	inset-block-start: 40px;
}
.jvectormap-goback {
	inset-block-end: 10px;
	z-index: 1000;
	padding: 6px;
}
.jvectormap-spinner {
	position: absolute;
	inset: 0;
	background: center no-repeat url(data:image/gif;base64,);
}
.jvectormap-legend-title {
	font-weight: bold;
	font-size: 14px;
	text-align: center;
}
.jvectormap-legend-cnt {
	position: absolute;
}
.jvectormap-legend-cnt-h {
	inset-block-end: 0;
	inset-inline-end: 0;
}
.jvectormap-legend-cnt-v {
	inset-block-start: 0;
	inset-inline-end: 0;
}
.jvectormap-legend {
	background: black;
	color: white;
	border-radius: 3px;
}
.jvectormap-legend-cnt-h .jvectormap-legend {
	float: start;
	margin-block-start: 0;
	margin-inline-end: 10px;
	margin-block-end: 10px;
	margin-inline-start: 0;
	padding-inline: 3px;
	padding-block-start: 3px;
	padding-block-end: 1px;
	.jvectormap-legend-tick {
		float: start;
	}
}
.jvectormap-legend-cnt-v .jvectormap-legend {
	margin-block-start: 10px;
	margin-inline-end: 10px;
	margin-block-end: 0;
	margin-inline-start: 0;
	padding: 3px;
}
.jvectormap-legend-cnt-h {
	.jvectormap-legend-tick {
		width: 40px;
	}
	.jvectormap-legend-tick-sample {
		height: 15px;
	}
}
.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
	height: 20px;
	width: 20px;
	display: inline-block;
	vertical-align: middle;
}
.jvectormap-legend-tick-text {
	font-size: 12px;
}
.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
	text-align: center;
}
.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
	display: inline-block;
	vertical-align: middle;
	line-height: 20px;
	padding-inline-start: 3px;
}