/* SmartWizard Basic CSS */

.sw-main {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
    border-radius: 0.25rem !important;

    .sw-container {
        display: block;
        margin: 0;
        padding: 0;
        position: relative;
    }

    .step-content {
        display: none;
        position: relative;
        margin: 0;
    }

    .sw-toolbar {
        margin-inline-start: 0;
    }
}

/* SmartWizard Theme: White */

.sw-theme-default {
    border: 1px solid #eff0f6;

    .sw-container {
        min-height: 250px;
    }

    .step-content {
        padding: 15px;
        border: 0px solid #eff0f6;
        background-color: #fff;
        text-align: start;
    }

    .sw-toolbar {
        background: #fff;
        border-radius: 0 !important;
        padding-inline-start: 10px;
        padding-inline-end: 10px;
        padding: 10px;
        margin-block-end: 0 !important;
    }

    .sw-toolbar-top {
        border-block-end-color	: #eff0f6 !important;
    }

    .sw-toolbar-bottom {
        border-block-start-color: #eff0f6 !important;
    }

    > ul.step-anchor > li {
        position: relative;
        margin-inline-end: 2px;

        > a {
            border: none !important;
            color: #5c6287 !important;
            text-decoration: none;
            outline-style: none;
            background: transparent !important;
            border: none !important;
            cursor: not-allowed;
            border-radius: 0 !important;

            &:hover {
                border: none !important;
                color: #5c6287 !important;
                text-decoration: none;
                outline-style: none;
                background: transparent !important;
                border: none !important;
                cursor: not-allowed;
                border-radius: 0 !important;
            }
        }

        &.clickable > a:hover {
            color: #2d66f7 !important;
            background: transparent !important;
            cursor: pointer;
        }

        > a::after {
            content: "";
            background: #2d66f7;
            height: 2px;
            position: absolute;
            width: 100%;
            inset-inline-start: 0px;
            inset-block-end: 0px;
            -webkit-transition: all 250ms ease 0s;
            transition: all 250ms ease 0s;
            -webkit-transform: scale(0);
            -ms-transform: scale(0);
            transform: scale(0);
        }

        &.active > a {
            border: none !important;
            color: #fff !important;
            background: #2d66f7 !important;
            cursor: pointer;

            &::after {
                -webkit-transform: scale(1);
                -ms-transform: scale(1);
                transform: scale(1);
            }
        }

        &.done > a {
            border: none !important;
            color: #5c6287 !important;
            background: transparent !important;
            cursor: pointer;

            &::after {
                background: #2d66f7;
                -webkit-transform: scale(1);
                -ms-transform: scale(1);
                transform: scale(1);
            }
        }

        &.danger > a {
            border: none !important;
            color: #d9534f !important;

            /* background: #d9534f !important; */
            cursor: pointer;

            &::after {
                background: #d9534f;
                border-inline-start-color: #f8d7da;
                -webkit-transform: scale(1);
                -ms-transform: scale(1);
                transform: scale(1);
            }
        }

        &.disabled > a {
            color: #eee !important;
            cursor: not-allowed;

            &:hover {
                color: #eee !important;
                cursor: not-allowed;
            }
        }
    }
}

/* Responsive CSS */
@media screen and (max-width: 768px) {
    .sw-theme-default > .nav-tabs > li {
        float: none !important;
    }
}

/* Common Loader */

.sw-loading {
    &::after {
        position: absolute;
        display: block;
        opacity: 1;
        content: "";
        inset-block-start: 0;
        inset-inline-start: 0;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 0.7);
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
        z-index: 2;
    }

    &::before {
        content: "";
        display: inline-block;
        position: absolute;
        inset-block-start: 50%;
        inset-inline-start: 50%;
        z-index: 10;
        border: 10px solid #f3f3f3;
        border-radius: 50%;
        border-block-start: 10px solid #3498db;
        width: 80px;
        height: 80px;
        margin-block-start: -40px;
        margin-inline-start: -40px;
        -webkit-animation: spin 1s linear infinite;

        /* Safari */
        animation: spin 1s linear infinite;
    }
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
/*--- WIZARD ELEMENTS ---*/

.wizard {
    border: 1px solid $border;
    background-color: $white;
    > {
        .steps {
            padding: 20px;
            > ul {
                padding: 0;
                margin-block-end: 0;
                display: flex;
                li {
                    float: none;
                    display: block;
                    width: auto;
                    .current-info {
                        display: none;
                    }
                    .title {
                        margin-inline-start: 5px;
                        white-space: nowrap;
                        transition: all 0.2s ease-in-out;
                        margin: 0;
                        min-height: auto;
                    }
                    + li {
                        margin-inline-start: 5px;
                    }
                }
            }
            a {
                color: $primary;
                font-weight: 500;
                font-size: 15px;
                display: flex;
                justify-content: center;
                align-items: center;
                &:hover,
                &:active {
                    color: $primary;
                    font-weight: 500;
                    font-size: 15px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                .number,
                &:hover .number,
                &:active .number {
                    flex-shrink: 0;
                    font-weight: 700;
                    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial,
                        "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
                        "Noto Color Emoji";
                    display: block;
                    text-align: center;
                    line-height: 2;
                    width: 30px;
                    height: 30px;
                    background-color: $primary-01;
                    border-radius: 100%;
                    transition: all 0.2s ease-in-out;
                    margin-inline-end: 5px;
                }
            }
            .disabled a {
                color: $primary-06;
                &:hover,
                &:active {
                    color: $primary-06;
                }
            }
            .current a {
                color: $primary;
                &:hover,
                &:active {
                    color: $primary;
                }
                .title,
                &:hover .title,
                &:active .title {
                    display: inline-block;
                }
                .number,
                &:hover .number,
                &:active .number {
                    background-color: $primary;
                    color: $custom-white;
                }
            }
            .done a {
                color: $success;
                &:hover,
                &:active {
                    color: $success;
                }
                .title,
                &:hover .title,
                &:active .title {
                    display: none;
                }
                .number,
                &:hover .number,
                &:active .number {
                    background-color: $success;
                    color: $custom-white;
                }
            }
        }
        .content {
            border-block-start: 1px solid $border;
            border-block-end: 1px solid $border;
            min-height: 150px;
            padding: 20px;
            > {
                .title {
                    font-size: 18px;
                    color: $default-color;
                    font-weight: 600;
                    padding-block-end: 15px;
                    display: none;
                    margin: 0;
                    &.current {
                        display: block;
                    }
                }
                .body {
                    float: none;
                    position: static;
                    width: auto;
                    height: auto;
                    input.parsley-error {
                        border-color: #ff5c77;
                        + ul {
                            list-style: none !important;
                        }
                    }
                }
            }
        }
        .actions {
            padding: 20px;
            > ul {
                margin: 0;
                padding: 0;
                list-style: none;
                display: flex;
                justify-content: space-between;
                > li:last-child a {
                    background-color: $success;
                }
            }
            a {
                display: block;
                background-color: $primary;
                padding: 9px 15px;
                line-height: 1.573;
                color: $custom-white;
                border-radius: 4px;
                font-weight: 500;
                &:hover,
                &:active {
                    display: block;
                    background-color: $primary;
                    padding: 9px 15px;
                    line-height: 1.573;
                    color: $custom-white;
                    border-radius: 4px;
                    font-weight: 500;
                }
            }
            .disabled a {
                background-color: $background;
                color: $default-color;
                &:hover,
                &:active {
                    background-color: $background;
                    color: $default-color;
                }
            }
        }
    }
    &.vertical > {
        .steps {
            padding: 20px;
            ul li {
                + li {
                    margin-inline-start: 10px;
                }
                .title {
                    display: none;
                }
            }
            .current a .title {
                display: inline-block;
            }
        }
        .content {
            margin: 0;
            padding: 20px;
        }
        .actions {
            padding: 20px;
            ul {
                float: none;
                margin: 0;
                padding: 0;
            }
        }
    }
}

